  --block-background-color: #eee;
  --block-border-color: #ddd;
  --content-block-element-margin-vertical: 0.625rem;
  --inputrange-thumb-border-color: #AAA;
  --text-link-color: #1280EF;
}

/*
* ----------------------------------------------------------------------------------------------
* focusable elements
*
* What are focusable elements?
* - https://www.deque.com/blog/give-site-focus-tips-designing-usable-focus-indicators/
* - https://stackoverflow.com/questions/1599660/which-html-elements-can-receive-focus
* ----------------------------------------------------------------------------------------------
*/
:focus {
  outline: 2px dotted var(--basic-focus-color);
}

button:focus,
.rd-button:focus,
.rd-pagination a:focus,
input:focus,
select:focus,
textarea:focus {
